<template>
  <div class="main-layout">
    <Modal />
    <div class="upper-section">
      <BannerPanel :connected="ebusConnected" />
      <!-- ebusUrl="ws://192.168.66.242:9001/mqtt" -->
      <EbusListener v-model="ebusConnected"
        @ebus-event="debugEbusEvent"
        @ebus-event-order-after-hint="orderAfterHint"
        @ebus-event-store-enter-END="storeEnterEND"
        @ebus-event-dispose-enter-END="disposeEnterEND"
        @ebus-event-sys-enter-INIT="sysEnterINIT"
        @ebus-event-sys-after-hint="sysAfterHint"
        @ebus-event-store-enter-PROCESS="storeEnterProcess"
        @ebus-event-sess-enter-IDLE="idle = true; clearSelection()"
        @ebus-event-reader-after-read="coded = true"
        @ebus-event-sess-enter-SESSION="idle = false"
      />
    </div>
    <div class="content-area">
      <SettingsPanel
        v-if="showSettings"
        :width="4"
        :height="4"
        :config="config"
        :isMultiselect="isMultiselect"
        :selectedId="selectedId"
        :gridStatus="gridStatus"
        :storeNetStatus="netStatus.storer"
        :pickupNetStatus="netStatus.retriever"
        :loading="settingsLoading"
        :initialIpMode="settingsNet.mode"
        :initialNet="{
          store_ip: settingsNet.store_ip,
          store_mask: settingsNet.store_mask,
          pickup_ip: settingsNet.pickup_ip,
          pickup_mask: settingsNet.pickup_mask,
          gw: settingsNet.gw
        }"
        @close="closeSettings"
        @save-network="saveNetwork"
        @select="handleSelect"
        @toggle-multiselect="toggleMultiselect"
        @store-order="storeOrder"
        @clear-order="clearOrder"
        @clear-selection="clearSelection"
      />
      <template v-else>
        <!--  Banner -->
        <!--  CabinSelector + CtrlPanel -->
        <div class="middle-section" @mousedown.self="clearSelection">
          <CabinSelector :width="4" :height="4" :config="config"
            :isMultiselect="isMultiselect"
            :selectedId="selectedId"
            :gridStatus="gridStatus"
            @select="handleSelect"
            />
          <CtrlPanel :isMultiselect="isMultiselect"
            @toggle-multiselect="toggleMultiselect"
            @store-order="storeOrder"
            @clear-order="clearOrder"
            @settings="openSettings"
          />
        </div>
        <div class="hint-area">
          <span>{{hint}}</span>
        </div>
        <!--  InfoPanel -->
        <div class="bottom-section">
          <InfoPanel v-if="currentView === 'info'"
            :status="infoStatus"
            :quantity="infoQuantity"
            :order_id="infoOrderId"
            :token="infoToken"
            :items="infoItems"
            :stored_time="infoStoredTime"
            :redeem_time="infoRedeemTime"
            :taken_time="infoTakenTime"
          />
          <KeyPad v-if="currentView === 'keypad'" @submit="handleSubmit" />
        </div>
        <div class="log-area">
          <span>{{log}}</span>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import BannerPanel from './components/BannerPanel.vue'
import CtrlPanel from './components/CtrlPanel.vue'
import CabinSelector from './components/CabinSelector.vue'
import InfoPanel from './components/InfoPanel.vue'
import SettingsPanel from './components/SettingsPanel.vue'
import EbusListener from '../../common/components/EbusListener.vue'
import Modal from '../../common/components/Modal.vue'
import { ModalService } from '../../common/services/modalService';
import KeyPad from '../../common/components/KeyPad.vue'
import ebusService from '../../common/services/ebusService.js'
import init from '../../common/mixins/init'

export default {
  name: 'App',
  mixins: [init],
  components: {
    BannerPanel,
    CtrlPanel,
    CabinSelector,
    InfoPanel,
    SettingsPanel,
    EbusListener,
    KeyPad,
    Modal
  },
  data() {
    return {
      showSettings: false,
      settingsNet: {
        mode: 'dhcp',
        store_ip: '', store_mask: '',
        pickup_ip: '', pickup_mask: '',
        gw: ''
      },
      coded: false,
      selected: false,
      /* BannerPanel */
      ebusConnected: false,
      /* CabinSelector */
      gridStatus: {},
      selectedId: [],
      config: [
        {"id":"9","w":2,"h":1},
        {"id":"5","w":1,"h":1},
        {"id":"1","w":1,"h":1},
        {"id":"10","w":2,"h":1},
        {"id":"6","w":1,"h":1},
        {"id":"2","w":1,"h":1},
        {"id":"11","w":2,"h":1},
        {"id":"7","w":1,"h":1},
        {"id":"3","w":1,"h":1},
        {"id":"12","w":2,"h":1},
        {"id":"8","w":1,"h":1},
        {"id":"4","w":1,"h":1},
      ],
      /* CtrlPanel */
      isMultiselect: false,
      /* KeyPad */
      deferredInput: null,
      /* InfoPanel */
      hint: "",
      currentView: "info",
      infoStatus: "",
      infoQuantity: 0, 
      infoOrderId: "",
      infoToken: "",
      infoItems: [],
      infoStoredTime: null,
      infoRedeemTime: null,
      infoTakenTime: null,
      log: "",
      /* ebusStatus */
      idle: true, // 
      netStatus: {
        storer: 'offline',
        retriever: 'offline'
      },
      restUrl: '',
      settingsLoading: false
    }
  },
  methods: {
    getRestUrl() {
      const params = new URLSearchParams(window.location.search);
      const rest = params.get('rest');
      return rest || `${window.location.origin}/app/rest`;
    },
    async loadNetworkConfig() {
      if (!this.restUrl) this.restUrl = this.getRestUrl();
      try {
        const res = await fetch(`${this.restUrl}/sys.cgi`);
        if (!res.ok) throw new Error(`HTTP ${res.status}`);
        const text = await res.text();
        let data = null;
        try {
          data = JSON.parse(text);
        } catch (err) {
          data = null;
        }
        const cfg = data && typeof data === 'object'
          ? (data.net_settings || data.net || data)
          : null;
        if (!cfg) return;
        const mode = cfg.mode === 'fixed' ? 'static' : cfg.mode;
        this.settingsNet = {
          mode: mode || this.settingsNet.mode,
          store_ip: cfg.storer_ip || cfg.store_ip || this.settingsNet.store_ip,
          store_mask: cfg.storer_mask || cfg.store_mask || this.settingsNet.store_mask,
          pickup_ip: cfg.retriever_ip || cfg.pickup_ip || this.settingsNet.pickup_ip,
          pickup_mask: cfg.retriever_mask || cfg.pickup_mask || this.settingsNet.pickup_mask,
          gw: cfg.gw || this.settingsNet.gw
        };
      } catch (err) {
        console.log('loadNetworkConfig failed', err);
      }
    },
    async openSettings(){
      this.showSettings = true;
      this.settingsLoading = true;
      await this.loadNetworkConfig();
      this.settingsLoading = false;
    },
    closeSettings(){
      this.showSettings = false;
    },
    saveNetwork(cfg){
      const set = {
        mode: cfg.mode,
        storer_ip: cfg.store_ip,
        storer_mask: cfg.store_mask,
        retriever_ip: cfg.pickup_ip,
        retriever_mask: cfg.pickup_mask,
        gw: cfg.gw
      };
      ebusService.send('sys/set', { act: 'net_setting', set });
      //  UI ( /etc/netplan  ifconfig)
      this.settingsNet = {
        mode: cfg.mode,
        store_ip: cfg.store_ip,
        store_mask: cfg.store_mask,
        pickup_ip: cfg.pickup_ip,
        pickup_mask: cfg.pickup_mask,
        gw: cfg.gw
      };
      this.log = `(${cfg.mode === 'dhcp' ? 'DHCP' : 'IP'})`;
    },
    handleChange(coded, selected) {
      if(!coded && !selected){
        this.hint="";
      }else if(!coded){
        if(this.currentView == "info"){
            this.hint="";
        }else if(this.currentView == "keypad"){
            this.hint="";
        }
      }else if(!selected){
        this.hint="";
      }
    },
    handleSelect(id) {
      if (this.isMultiselect) {
        if (this.selectedId.includes(id)) {
          this.selectedId = this.selectedId.filter(item => item !== id);
        } else {
          this.selectedId.push(id);
        }
      } else {
        //  id
        this.selectedId = [id];
      }
      this.log=""+id+"";
      ebusService.send('order/ordered',{"chid":this.selectedId});
      ebusService.send('store/goto_none');
      ebusService.send('store/start');
      ebusService.send('store/input',{"chid":this.selectedId});
      this.currentView='info';
      this.selected = true;
    },
    sysAfterHint(e) {
      const report = e?.arg?.net_status_report;
      if (report && report.status) {
        const status = String(report.status);
        const hid = String(report.hid || '');
        if (hid === 'storer') this.netStatus.storer = status;
        if (hid === 'retriever') this.netStatus.retriever = status;
        console.log(`net_status_report hid=${hid} status=${status}`);
      }
      if(!e.arg || !e.arg.gridStatus) return;
      const newStatus = e.arg.gridStatus;
      for (const [id, val] of Object.entries(newStatus)) {
        const current = this.gridStatus[id] || 0;

        //  bit (0,1)
        const keepBits01 = current & ((1 << 0) | (1 << 1));

        //  bit2 ~ bit8
        const newBits2to8 = val & 0b11111100;

        // 
        this.gridStatus[id] = keepBits01 | newBits2to8;
      }
    },
    debugEbusEvent(e) {
      try {
        const name = e?.e || 'unknown';
        console.log('ebus-event', name, e?.arg || {});
      } catch (err) {
        console.log('ebus-event [unserializable]');
      }
    },
    orderAfterHint(e) {
      if(!e.arg) return;
      try {
        for (const [id, data] of Object.entries(e.arg)) {
          const quantity = Number(data.quan);
          if(quantity > 0){
            this.gridStatus[id] |= (1<<0); // 
          }else{
            this.gridStatus[id] &= ~(1<<0); // 
          }
          this.infoStatus=quantity?'':'';
          this.infoQuantity=data.quan;
          this.infoOrderId=data.oid;
          this.infoToken=data.token;
          this.infoItems=data.items;
          this.infoStoredTime=data.stored_time;
          this.infoRedeemTime=data.redeem_time;
          this.infoTakenTime=data.taken_time;
        }
      } catch (err) {
          console.log(err);
      }
      this.log=JSON.stringify(e);
    },
    storeEnterEND(e) {
      if(e.on === 'store_ok'){
        ModalService.showInfo({message:''});
      }else{
        ModalService.showInfo({message:''});
      }
      ebusService.send('order/ordered',{"chid":this.selectedId});
      this.currentView='info';
      this.isMultiselect = false;
      this.clearSelection();
    },
    disposeEnterEND(e) {
      if(e.on === 'dispose_ok'){
        ModalService.showInfo({message:''});
      }else{
        ModalService.showInfo({message:''});
      }
      ebusService.send('order/ordered',{"chid":this.selectedId});
      this.isMultiselect = false;
      this.clearSelection();
    },
    sysEnterINIT() {
        ebusService.send('order/goto_MENU');
        ebusService.send('order/ordered',{"chid":this.config.map(item => item.id)});
    },
    clearSelection() {
      this.selected = false;
      this.selectedId = [];
      this.coded = false;
      ebusService.send('store/goto_none');
      ebusService.send('store/start');
    },
    toggleMultiselect(){
      this.isMultiselect = !this.isMultiselect;
      // 
      if (!this.isMultiselect) {
        this.selectedId = [this.selectedId[0]];
      }
    },
    storeOrder(){
      this.currentView='keypad';
      if(this.selectedId.length != 0){
        this.log=""+this.selectedId.sort().join(',')+"";
      }else{
        this.log="";
      }
    },
    clearOrder(){
      this.currentView='info';
      if(this.selectedId.length != 0){
        this.log=""+this.selectedId.sort().join(',')+"";
        ModalService.showYesNo('',
          ()=>{
            ModalService.showLocked();
            ebusService.send('dispose/goto_none');
            ebusService.send('dispose/start');
            ebusService.send('dispose/ready');
            ebusService.send('dispose/confirmed');
          },
          ()=>{}
        );
      }else{
        this.log="";
      }
    },
    handleSubmit(data) {
      ebusService.send('reader/read',{"type":"keypad","data":data});
    },
    storeEnterProcess() {
      ModalService.showLocked();
    },
    refreshSession() {
      if(this.idle){
        ebusService.send('sess/session_begin');
        this.idle=false
      }else{
        ebusService.send('sess/refresh_timer');
      }
    }
  },
  watch: {
    ebusConnected(val){
      if(val){
        ebusService.send('order/goto_MENU');
        ebusService.send('order/ordered',{"chid":this.config.map(item=>item.id)});
      }
    },
    coded(val){
      this.handleChange(val, this.selected);
    },
    selected(val){
      this.handleChange(this.coded, val);
    },
    currentView(nv,ov){
      if(nv!==ov && nv === "info"){
        //this.hint="";
      }else if(nv === "keypad"){
        //this.hint="";
      }
    }
  },
  mounted() {
    this.restUrl = this.getRestUrl();
    this.loadNetworkConfig();
    this.$nextTick(() => {
      if (!this.ebusConnected && ebusService.connected) {
        this.ebusConnected = true;
      }
    });
    document.addEventListener('mousedown', this.refreshSession);
     // 
    document.addEventListener('touchstart', function (e) {
      if (e.touches.length > 1) {
        e.preventDefault();
      }
    }, { passive: false });
  }
}
</script>

<style scoped>
.main-layout {
  display: flex;
  flex-direction: column;
  height: 100%;
  box-sizing: border-box;
  /*background-color: red;*/
  overflow: hidden;
}
.upper-section {
  outline: 1px solid #999;
  display: flex;
  flex-direction: column;
  height: 10vh;
}
.middle-section {
  outline: 1px solid #999;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  height: 35vh;
}
.hint-area {
  /*outline: 1px solid #999;*/
  height: 5vh;
  font-size: 5vw;
  display: flex;
  align-items: center;
  padding-left: 5vw;
}
.bottom-section {
  outline: 1px solid #999;
  height: 45vh;
  display: flex;
  flex-direction: column;
  padding: 3vw;
  box-sizing: border-box;
}
.operate-area {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: 1px solid #ccc;
  box-sizing: border-box;
  background-color: #fff;
  margin-bottom: 3vw;
  overflow: auto;
}
.log-area {
  outline: 1px solid #999;
  flex: 0 0 20px;
  font-size: 2vw;
  color: #555;
  white-space: nowrap;
  overflow: hidden;
  height: 5vh;
  text-overflow: ellipsis;
}
@import '../../common/css/common.css';
</style>
