<template>
  <div class="settings-root">
    <div v-if="loading" class="settings-loading">
      <div class="loading-card">...</div>
    </div>
    <!-- Top button row -->
    <div class="settings-topbar">
      <button class="topbtn" @click="$emit('close')"></button>
      <button class="topbtn" :class="{active: tab==='net'}" @click="tab='net'"></button>
      <button class="topbtn" :class="{active: tab==='locker'}" @click="tab='locker'"></button>
    </div>

    <!-- Content -->
    <div class="settings-body" :class="{locker: tab==='locker'}">
      <!-- Network -->
      <div v-if="tab==='net'" class="net-wrap" :class="{dhcp: ipMode==='dhcp'}">
        <div class="net-left">
          <div class="net-section">
            <div class="radio-row">
              <label class="radio">
                <input type="radio" value="dhcp" v-model="ipMode">
                <span>DHCP</span>
              </label>
              <label class="radio">
                <input type="radio" value="static" v-model="ipMode">
                <span> IP</span>
              </label>
            </div>
          </div>

          <div class="net-section">
            <div class="field-row">
              <div class="field-label">
                <span class="label-chip net-label" :class="netStatusClass(storeNetStatus)"></span>
              </div>
              <div class="field-inputs">
                <div class="ip-group">
                  <template v-for="(seg, idx) in form.store_ip" :key="'store_ip_'+idx">
                    <input
                      class="field ip-seg"
                      inputmode="numeric"
                      pattern="[0-9]*"
                      :tabindex="ipMode==='dhcp' ? -1 : 0"
                      :class="{focused: isActiveIp('store_ip', idx)}"
                      :value="seg"
                      @input="onSegInput('store_ip', idx, $event)"
                      @focus="setActiveIp('store_ip', idx); setCaretEnd($event)"
                      @click="setActiveIp('store_ip', idx); setCaretEnd($event)"
                      @keydown="setCaretEnd($event)"
                      :ref="el => setSegRef('store_ip', idx, el)"
                      placeholder="xxx">
                    <span class="dot" v-if="idx < 3">.</span>
                  </template>
                </div>
                <span class="slash">/</span>
                <input class="field mask"
                  inputmode="numeric"
                  pattern="[0-9]*"
                  :tabindex="ipMode==='dhcp' ? -1 : 0"
                  :class="{focused: isActiveMask('store_mask')}"
                  :value="form.store_mask"
                  @input="onMaskInput('store_mask', $event)"
                  @focus="setActiveMask('store_mask'); setCaretEnd($event)"
                  @click="setActiveMask('store_mask'); setCaretEnd($event)"
                  @keydown="setCaretEnd($event)"
                  :ref="el => setMaskRef('store_mask', el)"
                  placeholder="NN">
              </div>
            </div>

            <div class="field-row">
              <div class="field-label">
                <span class="label-chip net-label" :class="netStatusClass(pickupNetStatus)"></span>
              </div>
              <div class="field-inputs">
                <div class="ip-group">
                  <template v-for="(seg, idx) in form.pickup_ip" :key="'pickup_ip_'+idx">
                    <input
                      class="field ip-seg"
                      inputmode="numeric"
                      pattern="[0-9]*"
                      :tabindex="ipMode==='dhcp' ? -1 : 0"
                      :class="{focused: isActiveIp('pickup_ip', idx)}"
                      :value="seg"
                      @input="onSegInput('pickup_ip', idx, $event)"
                      @focus="setActiveIp('pickup_ip', idx); setCaretEnd($event)"
                      @click="setActiveIp('pickup_ip', idx); setCaretEnd($event)"
                      @keydown="setCaretEnd($event)"
                      :ref="el => setSegRef('pickup_ip', idx, el)"
                      placeholder="xxx">
                    <span class="dot" v-if="idx < 3">.</span>
                  </template>
                </div>
                <span class="slash">/</span>
                <input class="field mask"
                  inputmode="numeric"
                  pattern="[0-9]*"
                  :tabindex="ipMode==='dhcp' ? -1 : 0"
                  :class="{focused: isActiveMask('pickup_mask')}"
                  :value="form.pickup_mask"
                  @input="onMaskInput('pickup_mask', $event)"
                  @focus="setActiveMask('pickup_mask'); setCaretEnd($event)"
                  @click="setActiveMask('pickup_mask'); setCaretEnd($event)"
                  @keydown="setCaretEnd($event)"
                  :ref="el => setMaskRef('pickup_mask', el)"
                  placeholder="NN">
              </div>
            </div>

            <div class="field-row">
              <div class="field-label">
                <span class="label-chip">GW</span>
              </div>
              <div class="field-inputs">
                <div class="ip-group">
                  <template v-for="(seg, idx) in form.gw" :key="'gw_'+idx">
                    <input
                      class="field ip-seg"
                      inputmode="numeric"
                      pattern="[0-9]*"
                      :tabindex="ipMode==='dhcp' ? -1 : 0"
                      :class="{focused: isActiveIp('gw', idx)}"
                      :value="seg"
                      @input="onSegInput('gw', idx, $event)"
                      @focus="setActiveIp('gw', idx); setCaretEnd($event)"
                      @click="setActiveIp('gw', idx); setCaretEnd($event)"
                      @keydown="setCaretEnd($event)"
                      :ref="el => setSegRef('gw', idx, el)"
                      placeholder="xxx">
                    <span class="dot" v-if="idx < 3">.</span>
                  </template>
                </div>
              </div>
            </div>
          </div>

          <div class="net-hint">
            <span v-if="ipMode==='dhcp'">DHCPIP/MASK/GW</span>
            <span v-else>IP</span>
          </div>
        </div>

        <div class="net-right">
          <!-- Keypad -->
          <div class="keypad">
            <button class="kbtn" @click="onKey('1')">1</button>
            <button class="kbtn" @click="onKey('2')">2</button>
            <button class="kbtn" @click="onKey('3')">3</button>
            <button class="kbtn" @click="onKey('4')">4</button>
            <button class="kbtn" @click="onKey('5')">5</button>
            <button class="kbtn" @click="onKey('6')">6</button>
            <button class="kbtn" @click="onKey('0')">0</button>
            <button class="kbtn back" @click="backspace"></button>
            <button class="kbtn save" @click="save"></button>
          </div>
        </div>
      </div>

      <!-- Locker control -->
      <div v-else class="locker-wrap"
        @mousedown.self="$emit('clear-selection')"
        @touchstart.self="$emit('clear-selection')">
        <div class="locker-top"
          @mousedown.self="$emit('clear-selection')"
          @touchstart.self="$emit('clear-selection')">
          <div class="locker-left">
            <CabinSelector
              :width="width"
              :height="height"
              :config="config"
              :isMultiselect="isMultiselect"
              :selectedId="selectedId"
              :gridStatus="gridStatus"
              @select="$emit('select', $event)"
            />
          </div>
          <div class="locker-right">
            <CtrlPanel
              :isMultiselect="isMultiselect"
              mode="locker"
              @toggle-multiselect="$emit('toggle-multiselect')"
              @open-cabin="$emit('open-cabin')"
              @close-cabin="$emit('close-cabin')"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CabinSelector from './CabinSelector.vue'
import CtrlPanel from './CtrlPanel.vue'

const parseIp = (ip) => {
  if (!ip) return ['', '', '', '']
  const parts = String(ip).split('.')
  return [0, 1, 2, 3].map((i) => parts[i] || '')
}

export default {
  name: 'SettingsPanel',
  components: { CabinSelector, CtrlPanel },
  props: {
    // for locker control page
    config: { type: Object, default: () => ({}) },
    width: { type: Number, default: 4 },
    height: { type: Number, default: 4 },
    isMultiselect: { type: Boolean, default: false },
    selectedId: { type: Array, default: () => ([]) },
    gridStatus: { type: Object, default: () => ({}) },

    // optional initial network config
    storeNetStatus: { type: String, default: 'offline' }, // online | gateway | offline
    pickupNetStatus: { type: String, default: 'offline' }, // online | gateway | offline
    loading: { type: Boolean, default: false },
    initialIpMode: { type: String, default: 'dhcp' }, // 'dhcp' | 'static'
    initialNet: {
      type: Object,
      default: () => ({
        store_ip: '', store_mask: '',
        pickup_ip: '', pickup_mask: '',
        gw: ''
      })
    }
  },
  data() {
    return {
      tab: 'net',
      ipMode: this.initialIpMode,
      form: {
        store_ip: parseIp(this.initialNet.store_ip),
        store_mask: this.initialNet.store_mask || '',
        pickup_ip: parseIp(this.initialNet.pickup_ip),
        pickup_mask: this.initialNet.pickup_mask || '',
        gw: parseIp(this.initialNet.gw)
      },
      activeField: null,
      segRefs: {},
      maskRefs: {}
    }
  },
  watch: {
    ipMode(nv) {
      if (nv === 'dhcp') this.activeField = null
    },
    initialIpMode(nv) {
      this.ipMode = nv || 'dhcp'
    },
    initialNet: {
      deep: true,
      handler(nv) {
        const next = nv || {}
        this.form = {
          store_ip: parseIp(next.store_ip),
          store_mask: next.store_mask || '',
          pickup_ip: parseIp(next.pickup_ip),
          pickup_mask: next.pickup_mask || '',
          gw: parseIp(next.gw)
        }
      }
    }
  },
  methods: {
    setSegRef(field, idx, el) {
      if (!el) return
      if (!this.segRefs[field]) this.segRefs[field] = []
      this.segRefs[field][idx] = el
    },
    setMaskRef(field, el) {
      if (!el) return
      this.maskRefs[field] = el
    },
    isActiveIp(field, idx) {
      return this.activeField &&
        this.activeField.type === 'ip' &&
        this.activeField.field === field &&
        this.activeField.index === idx
    },
    isActiveMask(field) {
      return this.activeField &&
        this.activeField.type === 'mask' &&
        this.activeField.field === field
    },
    netStatusClass(status) {
      const s = String(status || '').toLowerCase()
      if (s === 'online') return 'net-online'
      if (s === 'gateway') return 'net-gateway'
      return 'net-offline'
    },
    setActiveIp(field, idx) {
      if (this.ipMode === 'dhcp') return
      this.activeField = { type: 'ip', field, index: idx }
      this.$nextTick(() => this.focusIp(field, idx))
    },
    setActiveMask(field) {
      if (this.ipMode === 'dhcp') return
      this.activeField = { type: 'mask', field }
      this.$nextTick(() => this.focusMask(field))
    },
    blurAll() {
      Object.values(this.segRefs).forEach((list) => {
        if (!list) return
        list.forEach((el) => el && el.blur && el.blur())
      })
      Object.values(this.maskRefs).forEach((el) => el && el.blur && el.blur())
    },
    focusIp(field, idx) {
      const el = this.segRefs[field]?.[idx]
      if (el) {
        el.focus()
        this.activeField = { type: 'ip', field, index: idx }
      }
    },
    focusMask(field) {
      const el = this.maskRefs[field]
      if (el) {
        el.focus()
        this.activeField = { type: 'mask', field }
      }
    },
    onKey(k) {
      if (this.ipMode === 'dhcp') return
      if (!this.activeField) return
      if (this.activeField.type === 'ip') {
        const { field, index } = this.activeField
        const cur = this.form[field][index] || ''
        if (cur.length >= 3) {
          this.pushOverflow(field, index, String(k))
          return
        }
        const next = cur + String(k)
        this.form[field].splice(index, 1, next)
        this.maybeAutoAdvanceIp(field, index, next.length)
      } else if (this.activeField.type === 'mask') {
        const field = this.activeField.field
        const cur = this.form[field] || ''
        if (cur.length >= 2) return
        this.form[field] = cur + String(k)
      }
    },
    backspace() {
      if (this.ipMode === 'dhcp') return
      if (!this.activeField) return
      if (this.activeField.type === 'ip') {
        const { field, index } = this.activeField
        const cur = this.form[field][index] || ''
        if (!cur && index > 0) {
          this.focusIp(field, index - 1)
          return
        }
        this.form[field].splice(index, 1, cur.slice(0, -1))
      } else if (this.activeField.type === 'mask') {
        const field = this.activeField.field
        const cur = this.form[field] || ''
        if (!cur) {
          if (field === 'store_mask') {
            this.focusIp('store_ip', 3)
          } else if (field === 'pickup_mask') {
            this.focusIp('pickup_ip', 3)
          }
          return
        }
        this.form[field] = cur.slice(0, -1)
      }
    },
    onSegInput(field, idx, e) {
      const raw = e?.target?.value ?? ''
      const digits = String(raw).replace(/\D/g, '')
      const next = digits.slice(0, 3)
      const overflow = digits.slice(3)
      this.form[field].splice(idx, 1, next)
      if (e?.target) e.target.value = next
      this.setCaretEnd(e)
      this.maybeAutoAdvanceIp(field, idx, next.length)
      if (overflow) this.pushOverflow(field, idx, overflow)
    },
    onMaskInput(field, e) {
      const raw = e?.target?.value ?? ''
      const digits = String(raw).replace(/\D/g, '')
      const next = digits.slice(0, 2)
      this.form[field] = next
      if (e?.target) e.target.value = next
      this.setCaretEnd(e)
    },
    maybeAutoAdvanceIp(field, index, len) {
      if (len < 3) return
      if (index < 3) {
        this.focusIp(field, index + 1)
      } else if (field === 'store_ip') {
        this.focusMask('store_mask')
      } else if (field === 'pickup_ip') {
        this.focusMask('pickup_mask')
      }
    },
    pushOverflow(field, index, chars) {
      const queue = String(chars).split('')
      let curField = field
      let curIndex = index
      while (queue.length) {
        if (curIndex < 3) {
          const nextIndex = curIndex + 1
          const curVal = this.form[curField][nextIndex] || ''
          if (curVal.length < 3) {
            const next = curVal + queue.shift()
            this.form[curField].splice(nextIndex, 1, next)
            this.focusIp(curField, nextIndex)
            if (next.length >= 3) {
              curIndex = nextIndex
              continue
            }
            return
          }
          curIndex = nextIndex
          continue
        }
        if (curField === 'store_ip') {
          const cur = this.form.store_mask || ''
          if (cur.length < 2) {
            this.form.store_mask = cur + queue.shift()
            this.focusMask('store_mask')
          }
          return
        }
        if (curField === 'pickup_ip') {
          const cur = this.form.pickup_mask || ''
          if (cur.length < 2) {
            this.form.pickup_mask = cur + queue.shift()
            this.focusMask('pickup_mask')
          }
          return
        }
        return
      }
    },
    setCaretEnd(e) {
      const el = e?.target
      if (!el || typeof el.setSelectionRange !== 'function') return
      const len = (el.value || '').length
      el.setSelectionRange(len, len)
    },
    save() {
      const joinIp = (parts) => {
        if (!parts || parts.every(p => !p)) return ''
        return parts.map(p => p || '').join('.')
      }
      this.$emit('save-network', {
        mode: this.ipMode,
        store_ip: joinIp(this.form.store_ip),
        store_mask: this.form.store_mask || '',
        pickup_ip: joinIp(this.form.pickup_ip),
        pickup_mask: this.form.pickup_mask || '',
        gw: joinIp(this.form.gw)
      })
    }
  }
}
</script>

