<template>
  <div class="ctrl-panel">
    <button class="ctrl-button" @click="$emit('toggle-multiselect')">
      {{ isMultiselect?'':''}}
    </button>
    <template v-if="mode==='locker'">
      <button class="ctrl-button" @click="$emit('open-cabin')"></button>
      <button class="ctrl-button" @click="$emit('close-cabin')"></button>
    </template>
    <template v-else>
      <button class="ctrl-button" @click="$emit('store-order')"></button>
      <button class="ctrl-button" @click="$emit('clear-order')"></button>
      <button class="ctrl-button" @click="$emit('settings')"></button>
    </template>
  </div>
</template>

<script>
export default {
  name: 'CtrlPanel',
  props: {
    isMultiselect: {
      type: Boolean,
      default: false
    },
    mode: {
      type: String,
      default: 'default' // 'default' | 'locker'
    }
  }
}
</script>

