<template>
  <div>
    <!-- Input Field -->
    <input v-model='inputValue' @input="enforceMaxLength" type="text" id="inputField"/>
    <!-- Keyboard -->
    <div class="keyboard" @mousedown="handleKeyClick">
      <div class="keyboard-row">
        <!-- Number keys -->
        <button id="key-1">1</button>
        <button id="key-2">2</button>
        <button id="key-3">3</button>
        <button id="key-4">4</button>
        <button id="key-5">5</button>
        <button id="key-6">6</button>
        <button id="key-7">7</button>
        <button id="key-8">8</button>
        <button id="key-9">9</button>
        <button id="key-0">0</button>
        <!--button id="key-backspace"> </button-->
        <button id="key-backspace" class="ckey"> </button>
      </div>
      <div class="keyboard-row">
        <!-- First row of letters -->
        <button id="key-Q">Q</button>
        <button id="key-W">W</button>
        <button id="key-E">E</button>
        <button id="key-R">R</button>
        <button id="key-T">T</button>
        <button id="key-Y">Y</button>
        <button id="key-U">U</button>
        <button id="key-I">I</button>
        <button id="key-O">O</button>
        <button id="key-P">P</button>
      </div>
  
      <div class="keyboard-row">
        <!-- Second row of letters -->
        <button id="key-A">A</button>
        <button id="key-S">S</button>
        <button id="key-D">D</button>
        <button id="key-F">F</button>
        <button id="key-G">G</button>
        <button id="key-H">H</button>
        <button id="key-J">J</button>
        <button id="key-K">K</button>
        <button id="key-L">L</button>
        <button id="key-enter" class="ckey"></button>
      </div>
  
      <div class="keyboard-row">
        <!-- Third row of letters -->
        <button id="key-Z">Z</button>
        <button id="key-X">X</button>
        <button id="key-C">C</button>
        <button id="key-V">V</button>
        <button id="key-B">B</button>
        <button id="key-N">N</button>
        <button id="key-M">M</button>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    emits: ['submit'],
    name: 'KeyPad',
    methods: {
      handleKeyClick(event) {
        const button = event.target.closest("button");
        if (!button) return;
        const id = button.id;
      	if (!id.startsWith('key-')) return;
        const key = button.textContent;
        if (id === "key-backspace") {
          this.inputValue = this.inputValue.slice(0, -1);
        } else if (id === "key-enter") {
          this.$emit("submit", this.inputValue);
        } else {
          if(this.inputValue.length < 12) {
            this.inputValue += key;
          }
        }
      },
      enforceMaxLength() {
        if (this.inputValue.length > 12) {
          this.inputValue = this.inputValue.slice(0, 12);
        }
      },
    },
    data() {
      return {
        inputValue: ''
      }
    }
  }
</script>

<style scoped>
  .keyboard-row {
    display: flex;
    margin-bottom: 1vw;
  }
  .keyboard-row button {
    margin: 0.5vw;
    padding: 1vw 1.3vw;
    font-size: 5vw;
    cursor: pointer;
  }
  #inputField {
    width: 80vw;
    font-size: 8vw;
    padding: 3vw;
    margin-bottom: 3vw;
  }
  .ckey {
    background-color: #ffdddd;
  }
</style>
