<template>
  <div>
    <!--  -->
    <div v-if="visible" class="modal-overlay" @mousedown="handleOverlayClick"></div>

    <!--  -->
    <div v-if="visible" class="modal" :class="{ 'sign-mode': mode === 'sign' }" @mousedown="handleOverlayClick">

      <!--  -->
      <div v-show="mode === 'sign'">
        <canvas
          ref="canvas"
          width="500"
          height="300"
          style="background:#eeeeee;"
          @mousedown="start"
          @mousemove="draw"
          @mouseup="stop"
          @mouseleave="stop"
          @touchstart.prevent="start"
          @touchmove.prevent="draw"
          @touchend.prevent="stop"
        ></canvas>
      </div>

      <div v-if="mode !== 'sign'" class="modal-content">{{ message }}</div>
      <button v-if="mode === 'info'" class="close-btn" @mousedown="close"></button>
      <button v-if="mode === 'yesno'" class="yes-btn" @mousedown="yes"></button>
      <button v-if="mode === 'yesno'" class="no-btn" @mousedown="no"></button>
      <div v-if="mode === 'sign'" class="sign-buttons">
        <button @click="clearCanvas"></button>
        <button @click="confirmSign"></button>
      </div>
    </div>
  </div>
</template>

<script>
import { modalBus } from '../services/modalService';

export default {
  data() {
    return {
      timer: null,
      visible: false,
      message: '',
      mode: 'info', // 'locked', 'info', 'yesno', 'sign'
      delay: 8000,
      yesCallback: null,
      noCallback: null,
      signCallback: null,
      ctx: null,
      isDrawing: false,
    };
  },
  methods: {
    showInfo(message='', delay = 8000){
      this.mode='info';
      this.message=message;
      this.visible=true;
      this.delay=delay;
      // 
      this.timer = setTimeout(() => {
        this.close();
      }, delay);
    },
    showLocked(message=''){
      this.mode='locked';
      this._clearTimer();
      this.message=message;
      this.visible=true;
    },
    showYesNo(message, yes, no){
      this.mode='yesno';
      this.message=message;
      this.yesCallback=yes;
      this.noCallback=no;
      this.visible=true;
    },
    showSign(callback) {
      this.mode = 'sign';
      this.visible = true;
      this.signCallback = callback;
      this.$nextTick(() => {
        this.initCanvas();
      });
    },
    initCanvas() {
      const canvas = this.$refs.canvas;
      if (!canvas) return;
      const parent = canvas.parentElement;
      const maxWidth = parent.clientWidth - 20; //  padding
      const width = Math.min(maxWidth, 500);    //  500
      const height = Math.floor(width * 0.6);   // 500x300  5:3 
      // 
      canvas.width = width;
      canvas.height = height;

      // 
      canvas.style.width = width + "px";
      canvas.style.height = height + "px";
      this.ctx = canvas.getContext('2d');
      this.ctx.lineWidth = 2;
      this.ctx.lineCap = 'round';
      this.ctx.strokeStyle = '#000';
    },
    yes() {
      if(typeof(this.yesCallback) === 'function') this.yesCallback();
      this.visible=false;
    },
    no() {
      if(typeof(this.noCallback) === 'function') this.noCallback();
      this.visible=false;
    },
    close() {
      this._clearTimer();
      this.visible=false;
      modalBus.emit('modalClosed');
    },
    handleOverlayClick() {
      if (this.mode === 'info') {
        this.close();
      }
    },
    _clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      }
    },
    // ===  ===
    getPos(e) {
      const rect = this.$refs.canvas.getBoundingClientRect();
      if (e.touches) {
        return {
          x: e.touches[0].clientX - rect.left,
          y: e.touches[0].clientY - rect.top,
        };
      } else {
        return {
          x: e.clientX - rect.left,
          y: e.clientY - rect.top,
        };
      }
    },
    start(e) {
      const pos = this.getPos(e);
      this.ctx.beginPath();
      this.ctx.moveTo(pos.x, pos.y);
      this.isDrawing = true;
    },
    draw(e) {
      if (!this.isDrawing) return;
      const pos = this.getPos(e);
      this.ctx.lineTo(pos.x, pos.y);
      this.ctx.stroke();
    },
    stop() {
      this.isDrawing = false;
      this.ctx.closePath();
    },
    clearCanvas() {
      const canvas = this.$refs.canvas;
      this.ctx.clearRect(0, 0, canvas.width, canvas.height);
    },
    confirmSign() {
      const canvas = this.$refs.canvas;
      const width = canvas.width;
      const height = canvas.height;
      const imageData = this.ctx.getImageData(0, 0, width, height);
      const data = imageData.data;

      let top = null, bottom = null, left = null, right = null;
      for (let y = 0; y < height; y++) {
        for (let x = 0; x < width; x++) {
          const i = (y * width + x) * 4;
          if (data[i + 3] > 0) {
            if (top === null || y < top) top = y;
            if (bottom === null || y > bottom) bottom = y;
            if (left === null || x < left) left = x;
            if (right === null || x > right) right = x;
          }
        }
      }

      if (top === null) {
        return;
      }

      const padding = 10;
      top = Math.max(0, top - padding);
      bottom = Math.min(height - 1, bottom + padding);
      left = Math.max(0, left - padding);
      right = Math.min(width - 1, right + padding);

      const croppedWidth = right - left + 1;
      const croppedHeight = bottom - top + 1;
      const croppedImageData = this.ctx.getImageData(left, top, croppedWidth, croppedHeight);

      const newCanvas = document.createElement('canvas');
      newCanvas.width = croppedWidth;
      newCanvas.height = croppedHeight;
      const newCtx = newCanvas.getContext('2d');
      newCtx.fillStyle = 'white';
      newCtx.fillRect(0, 0, croppedWidth, croppedHeight);
      newCtx.putImageData(croppedImageData, 0, 0);

      const base64 = newCanvas.toDataURL('image/png');
      if (typeof this.signCallback === 'function') this.signCallback(base64);
      this.close();
    },
  },
  created() {
    modalBus.on('showLocked', this.showLocked);
    modalBus.on('showInfo', info=>{this.showInfo(info.message,info.delay)});
    modalBus.on('showYesNo', msg=>{this.showYesNo(msg.message,msg.yes,msg.no)});
    modalBus.on('showSign', this.showSign);
  },
  mounted() {
    //  mounted  canvas
    if (this.$refs.canvas) {
      this.initCanvas();
    }
  }
}
</script>

<style scoped>
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5); /*  */
  z-index: 1000;
}

.modal {
  position: fixed;
  top: 40vh;
  left: 50vw;
  width: 60vw;
  transform: translate(-50%, -50%);
  padding: 5vw;
  background-color: white;
  box-shadow: 0 0.5vw 0.5vw rgba(0,0,0,0.1);
  font-size: 5vw;
  z-index: 1001;
}

.modal.sign-mode {
  width: 75vw;
}

.modal-content {
  margin-bottom: 3vw;
}

.modal button {
  font-size: 5vw;
  margin: 0 5vw;
}

.sign-buttons {
  display: flex;
  justify-content: space-around;
  margin-top: 4vw;
}

.close-btn {
  display: block;
}
</style>

