<template>
  <div
    class="locker-grid"
    :style="{
      display: 'grid',
      gridTemplateColumns: `repeat(${width}, 1fr)`,
      gridTemplateRows: `repeat(${height}, 1fr)`,
      gap: '2px',
      width: '100%',
      aspectRatio: `${width}/${height}`
    }"
  >
    <div
      v-for="(item) in layout" :key="item.id"
      class="locker-cell"
      :class="{ selected: isSelected(item.id) }"
      :style="{
        gridColumn: `${item.x + 1} / span ${item.w}`,
        gridRow: `${item.y + 1} / span ${item.h}`,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        fontWeight: 'bold',
        backgroundColor: getColor(gridStatus[item.id])
      }"
      @click="handleClick(item.id)"
    >
      {{ item.id }}
      <div class="status">
        <span
          v-for="(ch, idx) in Array.from(getStatusIndicator(gridStatus[item.id]))"
          :key="idx"
          class="status-char"
        >
          {{ ch }}
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CabinSelector',
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    },
    config: {
      type: Array,
      required: true
    },
    selectedId: {
      type: Array,
      default: () => []
    },
    isMultiselect: {
      type: Boolean,
    },
    // status bit:
    // (1<<0): stored 
    // (1<<1): expired 
    // (1<<2): opened 
    // (1<<3): occupied 
    // (1<<7): failure 
    gridStatus: {
      type: Object,
    }
  },
  data() {
    return {
      layout: []
    }
  },
  mounted() {
    this.generateLayout();
  },
  methods: {
    handleClick(id) {
      this.$emit('select', id);
    },
    isSelected(id) {
      return this.selectedId.includes(id);
    },
    generateLayout() {
      const grid = Array.from({ length: this.height }, () =>
        Array(this.width).fill(0)
      );
      const layout = [];
      this.config.forEach(({ id, w, h }) => {
        outer: for (let y = 0; y <= this.height - h; y++) {
          for (let x = 0; x <= this.width - w; x++) {
            let canPlace = true;
            for (let dy = 0; dy < h; dy++) {
              for (let dx = 0; dx < w; dx++) {
                if (grid[y + dy][x + dx] !== 0) {
                  canPlace = false;
                  break;
                }
              }
              if (!canPlace) break;
            }

            if (canPlace) {
              //  grid 
              for (let dy = 0; dy < h; dy++) {
                for (let dx = 0; dx < w; dx++) {
                  grid[y + dy][x + dx] = id;
                }
              }
              layout.push({ id, x, y, w, h });
              break outer;
            }
          }
        }
      });
      this.layout = layout;
    },
    getColor(status){
      return (status & (1<<0))?'#d0f0c0':'#eeeeee'; // occupied
    },
    getStatusIndicator(status) {
      let s = '';
      if(status & (1<<3)) s= '';
      if(status & (1<<7)) s= '';
      else if(status & (1<<1)) s+= '';
      else if(status & (1<<2)) s+= '';
      else s+= '';
      return s;
    }
  }
}
</script>

<style scoped>
.locker-grid {
  border: 0.5vw solid #888;
  width: 60vw;
  max-height: 30vh;
  margin: 3vw;
}

.locker-cell {
  box-sizing: border-box;
  border: 1px solid #999;
  transition: outline-color 0.2s;
  font-size: 5vw;
  position: relative;
}

.locker-cell.selected {
  outline: 0.7vw solid yellow;
  /*border-color: yellow;*/
}

.status {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-flow: row nowrap;
}

.status-char {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 1em;
  height: 1em;
  font-family: monospace;
  font-size: 3vw;
  font-weight: bold;
  line-height: 1;
  color: green;
}
</style>
